:root{--primary:34,34,34}html{-webkit-box-sizing:border-box;box-sizing:border-box;font-size:16px}@media only screen and (max-width:480px){html{font-size:100%}}*,:after,:before{-webkit-box-sizing:inherit;box-sizing:inherit}body,h1,h2,h3,h4,h5,h6,ol,p,ul{font-family:Poppins,Arial,Helvetica,sans-serif;font-size:16px;font-weight:400;margin:0;padding:0}ol,ul{list-style:none}img{height:auto;max-width:100%}a{text-decoration:none}body{border:1px solid #000;margin:0 auto;max-width:320px}h2{font-size:1.25rem;text-decoration:underline;text-transform:capitalize}@media screen and (min-width:30rem){h2{font-size:2.5rem}}.banner{background:url(/blog/static/banner-82736ec5c377a19e79bfdaeafbb0cf08.jpg) 0 0 no-repeat;background-size:cover;height:10rem}.banner-content{margin:.5rem;padding:3vh .5vw 0}.banner h1{font-size:1.875rem;font-weight:700}.banner h1,.banner p{background-color:hsla(0,0%,100%,.2);color:rgba(var(--primary),1)}.banner p{font-size:1rem;font-weight:600}.sales-container{padding:3vh 0}.product,.review-stars{text-align:center}.product{margin-bottom:2vh}.product img{max-width:20rem}.product button{background-color:rgba(var(--primary,1));border:none;color:#fff;cursor:pointer;padding:.5rem 1rem;text-transform:uppercase}.promo-list{padding:5vh 0}.promo-item{background:url(data:image/jpeg;base64,) 0 0 no-repeat;border:1px solid rgba(var(--primary),.1);padding:3vh 0;text-align:center}.promo-item a{border-bottom:.0625rem solid hsla(0,0%,100%,.9);display:inline-block;padding:1vh 0 .3vh}.promo-item a,.promo-item sub{color:hsla(0,0%,100%,.9);font-size:1rem}.promo-item sub{font-weight:400}.promo-item span{color:#fff;display:block;font-size:3.125rem}.promo-item-alt{background:#f8e0e8}.deal-container{background-color:#ededed;padding:5vh 0}.deal-container span{background-color:#c7c18f;color:#fff;font-size:1.25rem;font-weight:400;letter-spacing:.3rem;padding:.5rem;text-transform:uppercase}.deal-container h4{color:#212121;font-size:2.1875rem;margin-bottom:2vh}.deal-container p{color:#212